<template>
  <div v-if="model" class="page-article">
    <div class="d-flex py-3 px-1">
      <i class="iconfont icon-back  text-blue"></i>
      <strong class="flex-1 text-blue ">{{model.title}}</strong>
      <div class="text-grey fs-xs">2019-10-8</div>
    </div>
    <div>
        <div class=" px-3 body fs-lg" v-html="model.body"></div>
    </div>
  </div>
</template>
<script>
export default {
    props:{
        id:{required:true}
    },
    data(){
        return {
            model:null
        }
    },
    methods:{
    async fetch(){
        const res = await this.$http.get(`articles/${this.id}`)
        this.model = res.data
    }
    },
    created(){
         this.fetch()
    }
}
</script>
