<template>
  <div>
    <swiper :options="swiperOption">
      <swiper-slide>
        <img class="w-100" src="../assets/img/lb-1.jpeg" alt />
      </swiper-slide>
      <swiper-slide>
        <img class="w-100" src="../assets/img/lb-2.jpeg" alt />
      </swiper-slide>
      <swiper-slide>
        <img class="w-100" src="../assets/img/lb-3.jpeg" alt />
      </swiper-slide>
      <div class="swiper-pagination pagination-home text-right px-3" slot="pagination"></div>
    </swiper>
    <!--end-->
    <div class="nav-icons bg-white mt-3 text-center pt-3 text-grey-1">
      <div class="d-flex flex-wrap">
        <div class="nav-item mb-3" v-for="n in 10 " :key="n">
          <i class="sprite sprite-news"></i>
          <div>爆料站</div>
        </div>
      </div>
      <div class="bg-light py-2 fs-sm">
        <i class="sprite sprite-arrow mr-1"></i>
        <span>收起</span>
      </div>
    </div>

    <!--end icon-->
    <m-list-card icon="icon-cc-menu-circle" title="新闻资讯" :categories="newCats">
      <template #items="{category:category}">
        <router-link tag="div" :to="`/articles/${item._id}`" 
         class="py-2 fs-lg d-flex" v-for="(item,i) in category.newlist" :key="i">
          <span class="text-grey">[{{item.categoryname}}]</span>
          <span class="flex-1 text-dark text-ellipse pr-2">{{item.title}}</span>
          <span class="text-grey fs-sm">{{item.createdAt | date}}</span>
        </router-link>
       
      </template>
    </m-list-card>
    <m-list-card title="英雄列表" icon="icon-card-hero" :categories="heroCats">
      <template #items="{category:category}">
        <div class="d-flex flex-wrap " style="margin:0 -0.5rem;">
          <router-link :to="`/hero/${item._id}`" style="width:20%" class="p-2 text-center" v-for="(item,i) in category.herolist" :key="i">
            <img :src="item.avatar" class="w-100">
            <div>{{item.name}}</div>
          </router-link>
          
        </div>
      </template>
    </m-list-card>
    <m-card title="精彩视频" icon="icon-card-hero">1</m-card>
    <m-card title="图文攻略" icon="icon-news">1</m-card>
    <p>12</p>
    <p>12</p>
    <p>12</p>
    <p>12</p>
  </div>
</template>
<script>
import dayjs from "dayjs";
export default {
  filters: {
    date(val) {
      return dayjs(val).format("MM/DD");
    }
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".swiper-pagination"
        }
      },
      newCats: [],
      heroCats: []
    };
  },
  methods: {
    async fetchnewcats() {
      const res = await this.$http.get("news/list");
      this.newCats = res.data;
    },
    async fetchherocats() {
      const res = await this.$http.get("heroes/list");
      this.heroCats = res.data;
    }
  },
  created() {
    this.fetchnewcats();
    this.fetchherocats();
  }
};
</script>
